:root {
  --primary-color: #00acdc;
}

body {
  margin: 0;
}

.logo {
  height: 40px;
}

.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
}

nav a {
  color: #000;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

nav a:hover {
  color: var(--primary-color);
}

nav {
  align-items: center;
  height: 60px;
  display: flex;
}

nav ul {
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav li {
  height: 100%;
}

nav li:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: var(--primary-color);
}

.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
}
